___CSS_LOADER_EXPORT___.push([module.id, `* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    user-select: none;
}

body {
    font-family: 'Roboto', sans-serif;
    color: black;
    overflow: hidden;
    scroll-behavior: smooth;
}

#background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    z-index: -1;
}

header {
    margin: auto;
    font-size: 2.5vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 1vh solid rgb(255, 255, 255);
}

#headerCanvas {
    background-color: black;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: -1;
}

main {
    height: 100vh;
    width: 100vw;
    overflow-y: scroll;
}

#mainCanvas {
    background-color: rgb(0, 0, 0);
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: -1;
}

.project {
    border: 5px solid black;
    padding: 1vw;
    font-size: 1.5vw;
    transition: background-color 0.5s;
    cursor: default;
    width: 1000px;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    color: black;
}

.project:hover {
    background-color: rgba(174, 174, 178, 0.227);
}

